* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, Arial, Helvetica, sans-serif;
}

a {
  text-decoration: none;
}

nav {
  display: flex;
  justify-content: space-around;
  min-height: 8vh;
  position: absolute;
  width: 100%;
  align-items: center;
}

.navLinks {
  display: flex;
  list-style: none;
  justify-content: space-around;
  width: 50%;
  font-size: 24px;
}

.navLink {
  letter-spacing: 3px;
  font-weight: 400;
}

.navLink::after {
  content: " ";
  display: block;
  width: 0;
  height: 2px;
  background: rgb(255, 255, 255);
  transition: width 300ms;
}
.navLink:hover::after {
  width: 100%;
}

.navLink a {
  color: rgba(255, 255, 255, 0.85);
}
.navLink a:hover {
  color: rgba(255, 255, 255);
}

.logo {
  margin-right: 5vw;
  margin-left: 5vw;
  display: flex;
  color: rgba(255, 255, 255, 0.85);
  font-size: 32px;
}
.logo h4 {
  margin-left: 7px;
  letter-spacing: 1px;
  font-weight: lighter;
}
.fa-shopping-cart {
  color: white;
}

.main {
  background: radial-gradient(at center center, #2d7e9270 0%, #1c40617c 100%),
    url("./cover.jpg");
  height: 100vh;
  background-size: cover;
  background-position: center;
}

.hamburger-toggle {
  cursor: pointer;
  color: rgba(255, 255, 255);
  display: none;
}

@media screen and (max-width: 1250px) {
  .navLink {
    font-size: 20px;
  }
}

@media screen and (max-width: 1000px) {
  body {
    overflow-x: hidden;
  }

  .hamburger-toggle {
    display: block;
    font-size: 24px;
  }
  .navLinks {
    display: flex;
    position: absolute;
    right: 0px;
    top: 8vh;
    flex-direction: column;
    height: 92vh;
    align-items: center;
    width: 100%;
    background-color: #002e63;
    font-size: 32px;
    transform: translateX(100%);
    transition: transform 0.5s ease-in;
  }

  .active {
    transform: translateX(0%);
  }
}

/* end of nav section styling */
.showcase {
  max-width: 800px;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
}
.showcase h1 {
  font-size: 64px;
  color: white;
  font-weight: 500;
  letter-spacing: 3px;
  texr-align: center;
}

.showcase h2 {
  font-size: 28px;
  color: white;
  font-weight: 300;
  letter-spacing: 3px;
}

.showcase p {
  font-size: 16px;
  color: white;
  text-align: center;
  margin-top: 10px;
  margin-bottom: 45px;
}

.buyNow__btn {
  border: white solid 1px;
  background-color: transparent;
  color: white;
  padding: 10px 50px;
  margin: 0 10px;
  cursor: pointer;
}

.buyNow__btn:hover {
  color: black;
  background-color: white;
}
